.defaultBtn {
  width: 100px;
  height: 30px;
  font-family: 'Stick No Bills', sans-serif;
  border: 2px solid #0097d9;
  border-radius: 5px;
  background: transparent;
  cursor: pointer;
  font-size: 18px;
  outline: none;
  position: relative;
  z-index: 1;
  transition: background-color 0.5s;
  text-transform: uppercase;
}

.defaultBtn:before {
  content: '';
  position: absolute;
  top: -1px;
  left: 50%;
  right: 50%;
  bottom: -1px;
  opacity: 0;
  background-color: #0097d9;
  transition: left 0.3s ease-in-out, right 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.defaultBtn:hover:before {
  left: -1px;
  right: -1px;
  opacity: 1;
  z-index: -1;
  transition: left 0.3s ease-in-out, right 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.red {
  width: 100px;
  height: 30px;
  font-family: 'Stick No Bills', sans-serif;
  border: 2px solid #0097d9;
  border-radius: 5px;
  background-color: tomato;
  cursor: pointer;
  font-size: 18px;
  outline: none;
  position: relative;
  z-index: 1;
  transition: background-color 0.5s;
  text-transform: uppercase;
}

.red:before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  background-color: green;
  transition: left 0.3s ease-in-out, right 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.red:hover:before {
  left: 0;
  right: 0;
  opacity: 1;
  z-index: -1;
  transition: left 0.3s ease-in-out, right 0.3s ease-in-out, opacity 0.3s ease-in-out;
}


